.login{
    display: flex;
    background-color: white;
    height: 100vh;
    flex-direction: column;
    align-items: center;
}

.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
}


.login__logo{
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100px;
    object-fit: contain;
 
}
