.login__container >h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container > form >h5{
    margin-bottom: 5px;
}
.login__container > form > input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}
.login__container > p{
    margin-top: 15px;
    font-size: 12px;
}
.login__container>form>button{
    align-items: center;
}
.login__signInButton{
    border-radius: 2px;
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    height: 30px;
    width: 100%;
}
.login__registerButton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    border-color: darkgray;
    margin-top: 10px;
}