.subtotal{
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 300px;
    height: 100px;
    justify-content: space-between;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}

.subtotal__gift{
    display: flex;
    align-items: center;
}

.subtotal__gift >input{
    margin-right: 5px;
}

.subtotal > button{
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    height: 25px;
}
